import React from "react";
import styled, { css } from "styled-components";
import SelectToolButton from "../../buttons/selectToolButton/SelectToolButton";
import { ReactComponent as BrushIcon } from "../../../assets/icons/brush-icon.svg";
import { ReactComponent as EraserIcon } from "../../../assets/icons/eraser-icon.svg";
import { ReactComponent as ColorizeIcon } from "../../../assets/icons/colorize-icon.svg";
import { ReactComponent as FillIcon } from "../../../assets/icons/fill-icon.svg";
import { ReactComponent as CircleIcon } from "../../../assets/icons/circle-icon.svg";
import { ReactComponent as SquareIcon } from "../../../assets/icons/square-icon.svg";
import { ReactComponent as LineIcon } from "../../../assets/icons/line-icon.svg";
import { ReactComponent as PenToolIcon } from "../../../assets/icons/pen-tool-icon.svg";
import { ReactComponent as DeleteIcon } from "../../../assets/icons/delete-icon.svg";
import { ReactComponent as DownloadIcon } from "../../../assets/icons/download-icon.svg";

const Root = styled.div`
  display: flex;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  padding: 8px;
`;

const Tools = styled.div`
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 4px;
`;

const IconSharedStyle = css`
  fill: ${(props) =>
    props.selected ? "#ffffff" : props.disabled ? "grey" : "#580c60"};
  width: 24px;
  height: 24px;
  transition: 0.3s;
`;

const StyledBrushIcon = styled(BrushIcon)`
  ${IconSharedStyle}
`;

const StyledEraserIcon = styled(EraserIcon)`
  ${IconSharedStyle}
`;

const StyledColorizeIcon = styled(ColorizeIcon)`
  ${IconSharedStyle}
`;

const StyledFillIcon = styled(FillIcon)`
  ${IconSharedStyle}
`;

const StyledCircleIcon = styled(CircleIcon)`
  ${IconSharedStyle}
`;

const StyledSquareIcon = styled(SquareIcon)`
  ${IconSharedStyle}
`;

const StyledLineIcon = styled(LineIcon)`
  ${IconSharedStyle}
  width: 36px;
  height: 36px;
`;

const StyledPenToolIcon = styled(PenToolIcon)`
  ${IconSharedStyle}
`;

const StyledDeleteIcon = styled(DeleteIcon)`
  ${IconSharedStyle}
`;

const StyledDownloadIcon = styled(DownloadIcon)`
  ${IconSharedStyle}
`;

export default function SelectToolDialog({
  selectFunction,
  toolSelected,
  clearFunction,
}) {
  function handleToolClick(tool) {
    selectFunction(tool);
  }

  return (
    <Root>
      <Tools>
        <SelectToolButton
          selected={toolSelected === "brush"}
          onClick={() => handleToolClick("brush")}
        >
          <StyledBrushIcon selected={toolSelected === "brush"} />
        </SelectToolButton>
        <SelectToolButton onClick={() => handleToolClick("brush")}>
          <StyledCircleIcon />
        </SelectToolButton>
        <SelectToolButton onClick={() => handleToolClick("brush")}>
          <StyledSquareIcon />
        </SelectToolButton>
        <SelectToolButton onClick={() => handleToolClick("brush")}>
          <StyledLineIcon />
        </SelectToolButton>
        <SelectToolButton onClick={() => handleToolClick("brush")}>
          <StyledPenToolIcon />
        </SelectToolButton>

        <SelectToolButton
          selected={toolSelected === "eraser"}
          onClick={() => handleToolClick("eraser")}
        >
          <StyledEraserIcon selected={toolSelected === "eraser"} />
        </SelectToolButton>
        <SelectToolButton onClick={() => handleToolClick("brush")}>
          <StyledColorizeIcon />
        </SelectToolButton>
        <SelectToolButton onClick={() => handleToolClick("brush")}>
          <StyledFillIcon />
        </SelectToolButton>
        <SelectToolButton onClick={() => handleToolClick("brush")}>
          <StyledDownloadIcon />
        </SelectToolButton>
        <SelectToolButton onClick={clearFunction}>
          <StyledDeleteIcon />
        </SelectToolButton>
      </Tools>
    </Root>
  );
}
