import React from "react";
import styled, { css } from "styled-components";
import useLongPress from "../../../hooks/useLongPress";
import SelectToolButton from "../../buttons/selectToolButton/SelectToolButton";

import { ReactComponent as BrushIcon } from "../../../assets/icons/brush-icon.svg";
import { ReactComponent as EraserIcon } from "../../../assets/icons/eraser-icon.svg";
import { ReactComponent as UndoIcon } from "../../../assets/icons/undo-icon.svg";
import { ReactComponent as RedoIcon } from "../../../assets/icons/redo-icon.svg";

const Root = styled.div`
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  padding: 8px 2%;
  justify-content: space-around;
  background-color: #fffade;
  -webkit-box-shadow: 0px 9px 17px 4px rgba(0, 0, 0, 0.43);
  box-shadow: 0px 9px 17px 4px rgba(0, 0, 0, 0.43);
`;

const ButtonContainer = styled.div`
  width: 22%;
  height: 90%;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const ColorButton = styled.div`
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: ${(props) => (props.color ? props.color : "#000")};
`;

const IconSharedStyle = css`
  fill: ${(props) =>
    props.selected ? "#ffffff" : props.disabled ? "grey" : "#580c60"};
  width: 24px;
  height: 24px;
  transition: 0.3s;
`;

const StyledUndoIcon = styled(UndoIcon)`
  ${IconSharedStyle}
`;

const StyledRedoIcon = styled(RedoIcon)`
  ${IconSharedStyle}
`;

const StyledBrushIcon = styled(BrushIcon)`
  ${IconSharedStyle}
`;

const StyledEraserIcon = styled(EraserIcon)`
  ${IconSharedStyle}
`;

export default function SmallScreenCanvasMenus({
  colorSelected,
  toolSelected,
  openColorDialog,
  openToolDialog,
  toolButtonSelected,
  undoFunction,
  redoFunction,
  canvasActions,
  cStep,
}) {
  const colorLongPress = () => {
    return;
  };

  const colorClick = () => {
    openColorDialog();
    return;
  };

  const colorLongPressEvent = useLongPress(colorLongPress, colorClick);

  return (
    <Root>
      <ButtonContainer>
        <SelectToolButton>
          <ColorButton {...colorLongPressEvent} color={colorSelected} />
        </SelectToolButton>
      </ButtonContainer>

      <ButtonContainer>
        <SelectToolButton
          selected={toolButtonSelected}
          onClick={openToolDialog}
        >
          {
            {
              brush: <StyledBrushIcon selected={toolButtonSelected} />,
              eraser: <StyledEraserIcon selected={toolButtonSelected} />,
            }[toolSelected]
          }
        </SelectToolButton>
      </ButtonContainer>

      <ButtonContainer>
        <SelectToolButton onClick={undoFunction} disabled={cStep <= 0}>
          <StyledUndoIcon disabled={cStep <= 0} />
        </SelectToolButton>
      </ButtonContainer>

      <ButtonContainer>
        <SelectToolButton
          onClick={redoFunction}
          disabled={canvasActions.length <= cStep + 1}
        >
          <StyledRedoIcon disabled={canvasActions.length <= cStep + 1} />
        </SelectToolButton>
      </ButtonContainer>
    </Root>
  );
}
